.history-container{
    position: relative;
    top: 100px;
    min-height: calc(100vh - 100px);
}

.history-container .card:nth-child(odd){
    left: 10%;
    margin-right: auto;
}

body{
    overflow: paged-y;
}

.history-container .card:nth-child(even){
    right: 10%;
    margin-left: auto;
}

.history-header{
    position: relative;
    top: 0;
    width: 100%;
    min-height: 58px;
}

.history-title{
    position: absolute;
    left: 50px;
    margin-top: 5%;
    font-size: 40px;
    font-family: "Droid Sans";
    color: #f7f6f9;
    text-shadow: #282c34 1px 1px 8px;
}

.history{

}



.history-content-card{
    position: relative;
    margin-top: 5%;
    top: 100px;
    /*left: 20%;*/
    min-height: 150px;
    width: 60%;
    background-color: #20c4fb;
    border-radius: 10px;
}

.history-content-card p{
    padding: 10px 20px;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

