.guideline {
  position: fixed;
  z-index: 10;
  width: auto;
  top: auto;
  left: 50%;
  bottom: 32px;
  -webkit-transform: translate3d(-50%, 0, 0);
  transform: translate3d(-50%, 0, 0);
}

.guideline ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}

.guideline ul li{
  height: auto;
  margin: 0 28px;
}

.guideline ul li a{
  color: #FFF;
}
