.container-title {
  /* background-color: lightgoldenrodyellow; */
  position: absolute;
  left: 0;
  margin-left: calc(100%/10);
  padding-top: 150px;
  z-index: 100;
}

.my-name {
  font-size: 6rem;
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 1.6rem;
  font-family: "futura_m";
  font-weight: normal;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}

.my-job{
  /* color: #fff; */
  font-size: 2.8rem;
  line-height: 1.5;
  letter-spacing: 0.1em;
}

.left-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 29.427%;
  margin: auto;
  height: 1385px;
  width: 1385px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.grab-content{
  position: relative;
  width: 39.350180505%;
  height: 39.350180505%;
}

.animate-container {
  transform: translate3d(-10.4px, 6.6px, 0px);
  transform-style: preserve-3d;
  backface-visibility: hidden;
  position: relative;
  display: block;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
}

.left-text-container{
  position: absolute;
  left: -15.5963%;
  top: 50%;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
  z-index: 101;
}

.left-text-container p{
  font-size: 6.4rem;
  letter-spacing: 0.1em;
  text-shadow: 0 10px 15px rgba(2, 11, 22, 0.2);
  color: lightcyan;
  font-family: "futura_b";
  z-index: 101;
}

.pic01-text-container img{
    z-index: 100;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    fill: lightcoral;
    /* border-radius: 30%; */
    /* object-fit: cover; */
}
