.name {
  font-size: 2.4rem;
  letter-spacing: 0.1em;
  position: relative;
  z-index: 1000;
}

.wrap-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 60%;
}

.icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: 6.4rem;
  margin-left: auto;
}

.icons a{
  display: block;
  margin-right: 3.2rem;
  font-size: 2.4rem;
  letter-spacing: 0.1em;
}

.icons a:hover{
  opacity: 0.8;
  -ms-transform: scale(1.2, 1.2); /* IE 9 */
  -webkit-transform: scale(1.2, 1.2); /* Safari */
  transform: scale(1.2, 1.2);
  transition: .6s;
}

.icons a img{
    width: 50px;
    height: 50px;
    /* fill: #FFF; */
}

.menu-icon {
  position: relative;
  min-width: 50px;
  padding-right: 30px;
  height: 22px;
  cursor: pointer;
  /* z-index: 200; */
}

.menu-icon img{
    text-align: right;
}

header a {
  color: #000;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

/******* navbar  *******/
.global-nav__list {
  width: 300px;
  height: 100vh;
  background: #20c4fb;
  padding: 30px;
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  position: fixed;
  right: 0;
  top: 0;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.4);
}

.item {
  background: #43415f;
  border-radius: 5px;
  height: 40px;
  width: 100%;
  opacity: 0;
  margin-bottom: 10px;
  transform-origin-y: 0%;
  color: white;
}
.item a{
  padding: 10px;
}
.item:nth-child(1) {
  background: #f69a9a;
}

.item:nth-child(2) {
  background: #83ae9b;
}

.item:nth-child(3) {
  background: #ef4566;
}

.item:nth-child(4) {
  background: #f9cdae;
}

.item:nth-child(5) {
  background: #c8c8a9;
}

.closed-navbar{
  color: black;
  margin-left: auto;
  margin-bottom: 20px;
  cursor: pointer;
}
